.event-card {
    margin-top: 20px;


    background-color: var(--event-card-background-color);
    border-radius: 4px;
    width: 352px;
    height: 100px;
    display: flex;
}

.event-card__content {
    width: 70%;
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.event-card__name {
    margin-top: 10px;
    margin-left: 14px;
    width: 100%;
    word-wrap: break-word;
    color: var(--event-card-title-color);
    font-size: 16px;
}

.event-card__time {
    margin-left: 14px;
    margin-bottom: 12px;
    width: 100%;
    word-wrap: break-word;
    color: var(--main-accented-color);
    font-size: 14px;
}

.event-card__icon {
    height: 100%;
    width: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.event-card__icon > svg {
    height: 60px;
    width: 60px;
}